section.main-section .contact {
  position: absolute;
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  color: #fff;
  top: 360px;
  left: 40px;
  z-index: 4;
  h1{
    font-weight: 700;
    transform: skew(-15deg, 0deg);
    margin-bottom: 6px;
  }
  & .address{
    display: block;
    margin-bottom: 60px;
  }

  & a{
    display: block;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    margin-bottom: 14px;
    margin-left: 20px;
  }

  & a.phone::before {
    content: "";
    background: url(../images/phone.svg) no-repeat;
    background-size: 100%;
    padding: 20px 22px 0 0;
  }

  & a.email::before {
    content: "";
    background: url(../images/email.svg) no-repeat;
    background-size: 100%;
    padding: 0px 22px 0 0;
  }

  & a span {
    margin-left: 22px;
  }
}

section.main-section .about {
  position: absolute;
  line-height: 1.5;
  top:720px;
  left: 90px;
  width: 640px;
  height: 100px;
  font-family: 'Play', sans-serif;
  font-size: 18px;
  z-index: 41;
  & span {
    font-weight: 700;
   // font-style: italic;
  }
}

section.main-section .menu {
  position: absolute;
  top: 160px;
  right: 0;
  width: 190px;
  text-transform: uppercase; 
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  z-index: 100;
  & ul {}
  & li {
    list-style-type: none;
    padding-bottom: 10px;
    font-weight: 600;
  }
  & li::before {
    //content: "";
  }

  & a {
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    &:hover{
      font-weight: 700;
    }
  }
}